<template>
    <div class="box">
    	<div class="title">
			
		</div>
		<div class="title-box">
			<span>:</span>
			<select class="form-inline gift-seek" required="required">
				<option v-for="item in uidType" :value="item.name">{{item.title}}</option>
			</select>
			<input type="text" name="" id="input" class="form-control" value="" required="required" pattern="" title="">
			<a href="javascript:void(0)" class="btn btn-success seek-btn" @click="">
				<i class="iconfont icon-fangdajing"></i>
				<span></span>
			</a>
		</div>
		<div class="account-main">
			<div class="table-responsive  playerInfo">
				<div class="account-list">
					
				</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in playerInfo">{{item.title}}</th>
						</tr>
						<tr>
							<td v-for="item in playerInfo">1</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="table-responsive  playerInfo col-md-6" style="padding-right:5px;">
				<div class="account-list">
					
				</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in resource">{{item.title}}</th>
						</tr>
						<tr>
							<td v-for="item in resource">1</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="table-responsive  playerInfo col-md-6" style="padding-left:5px;">
				<div class="account-list">
					
				</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in sctInfo">{{item.title}}</th>
						</tr>
						<tr>
							<td v-for="item in sctInfo">1</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="clearit"></div>
			<div class="table-responsive  playerInfo">
				<div class="account-list">
					
				</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in bortlevel">{{item.title}}</th>
						</tr>
						<tr>
							<td v-for="item in bortlevel">1</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="table-responsive  playerInfo">
				<div class="account-list">
					
				</div>
				<table class="table">
					<tbody>
						<tr>
							<th v-for="item in warShipLevel">{{item.title}}</th>
						</tr>
						<tr>
							<td v-for="item in warShipLevel">1</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
	</div>
</template>

<script type="ecmascript-6">
    export default{
    	data(){
    		return{
				uidType:[
					{title:'uid',name:'sailcraft'},
					{title:'id',name:'uq'}
				],
    			playerInfo:[
    				{title:'uid'},
    				{title:'id'},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''}
    			],
    			resource:[
    				{title:'GEM'},
    				{title:'GEM'},
    				{title:''},
    				{title:''}
    			],
    			sctInfo:[
    				{title:'PVP'},
    				{title:''}
    			],
    			bortlevel:[
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:''},
    				{title:'1'},
    				{title:'2'},
    			],
    			warShipLevel:[
    				{title:''},
    				{title:''},
    				{title:''}
    			]
    		}
    	}
    }
</script>

<style rel="stylesheet" lang="scss">
	@import "../assets/css/main.scss";
	.box{
		.account-main{
			.playerInfo{
				position:static;
				margin-bottom:10px;
				&.col-md-6{
					width:50%;
					float:left;
					padding:0;

				}
				.account-list{
				height:50px;
				line-height:50px;
				color:#fff;
				background:rgba(0,0,0,0.3);
				font-size:14px;
				padding-left:15px;
			}
			table{
				margin-bottom:0px;
				background:rgba(0,0,0,0.3);
				margin-top:1px;
				tr{
					th{
						font-size:14px;
					}
					td,th{
						border:none;
						text-align:center;
						border:1px solid rgba(255,255,255,0.2);
						vertical-align: middle;
						a{
							color:#fff;
						}

					}
				}
			}

			}
		}
	}
</style>


